'use client';

import React, { useState } from 'react';
import api from '@/lib/api';

export default function TestAPI() {
  const [status, setStatus] = useState('Ready');

  const testAuthentication = async () => {
    try {
      setStatus('Registering...');
      console.log('üîê Step 1: Registering user...');

      // Register a new user
      const registerResponse = await fetch('http://localhost:8000/api/v1/store/auth/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify({
          phone_number: '01345678902',
          password: 'password123'
        })
      });

      const registerData = await registerResponse.json();
      console.log('üìù Registration response:', registerData);

      if (registerData.otp_code) {
        setStatus(`Verifying OTP: ${registerData.otp_code}`);
        console.log('üîê Step 2: Verifying OTP...');

        // Verify OTP
        const verifyResponse = await fetch('http://localhost:8000/api/v1/store/auth/verify-otp', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
          },
          body: JSON.stringify({
            phone_number: '01345678902',
            otp_code: registerData.otp_code
          })
        });

        const verifyData = await verifyResponse.json();
        console.log('‚úÖ Verification response:', verifyData);

        if (verifyData.token) {
          setStatus('Token received! Testing API...');
          console.log('üîê Step 3: Storing token and testing API...');

          // Store token in localStorage
          localStorage.setItem('auth_token', verifyData.token);
          console.log('üíæ Token stored in localStorage:', verifyData.token);

          // Test authenticated API call
          const meResponse = await api.getMe();
          console.log('‚úÖ Authenticated API call success:', meResponse);
          setStatus('‚úÖ Success! Authenticated');

          // Test manual API call with token
          console.log('üß™ Testing manual authenticated API call...');
          const manualToken = localStorage.getItem('auth_token');
          console.log('üß™ Manual token check:', manualToken);

          const manualResponse = await fetch('http://localhost:8000/api/v1/store/account/me', {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json',
              'Authorization': `Bearer ${manualToken}`
            }
          });

          console.log('üß™ Manual response status:', manualResponse.status);
          const manualData = await manualResponse.json();
          console.log('üß™ Manual response data:', manualData);
          setStatus('‚úÖ All tests passed!');

        } else {
          setStatus('‚ùå Verification failed');
        }
      } else {
        setStatus('‚ùå Registration failed');
      }

    } catch (error: any) {
      console.error('‚ùå Authentication test failed:', error);
      setStatus(`‚ùå Error: ${error.message}`);
    }
  };

  const clearToken = () => {
    localStorage.removeItem('auth_token');
    setStatus('Token cleared');
    console.log('üóëÔ∏è Token removed from localStorage');
  };

  const testCurrentToken = async () => {
    const token = localStorage.getItem('auth_token');
    console.log('üß™ Current token:', token);

    if (!token) {
      setStatus('No token found');
      return;
    }

    try {
      const response = await fetch('http://localhost:8000/api/v1/store/account/me', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': `Bearer ${token}`
        }
      });

      const data = await response.json();
      console.log('üß™ API Response:', data);

      if (response.ok) {
        setStatus('‚úÖ Token valid');
      } else {
        setStatus(`‚ùå ${data.message}`);
      }
    } catch (error: any) {
      console.error('‚ùå Token test failed:', error);
      setStatus(`‚ùå Error: ${error.message}`);
    }
  };

  return (
    <div className="fixed top-4 right-4 bg-blue-500 text-white p-3 rounded text-xs z-50 max-w-xs">
      <div className="space-y-2">
        <div className="font-bold">üß™ API Test Suite</div>
        <div className="text-yellow-200">Status: {status}</div>

        <div className="space-y-1">
          <button
            onClick={testAuthentication}
            className="bg-green-600 hover:bg-green-700 px-2 py-1 rounded w-full text-left"
          >
            üîê Test Full Auth Flow
          </button>

          <button
            onClick={testCurrentToken}
            className="bg-blue-600 hover:bg-blue-700 px-2 py-1 rounded w-full text-left"
          >
            üß™ Test Current Token
          </button>

          <button
            onClick={clearToken}
            className="bg-red-600 hover:bg-red-700 px-2 py-1 rounded w-full text-left"
          >
            üóëÔ∏è Clear Token
          </button>
        </div>
      </div>
    </div>
  );
}